
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import Card from 'vux/src/components/card/index.vue'


export default{
mixins: [ common ],
components: {
Card
},
data () {
return {
info: {},
map_sex: {0:'未确定',1:'男',2:'女'},
map_student_type: {0:'体验学员 ',1:'正式学员 ',2:'vip学员'},
map_rel_text: {0:'未设置',1:'自己',2:'爸爸',3:'妈妈',4:'其他'}
}
},
mounted () {
this.init_data()
},
methods: {
init_data () {
let uri = util.sprintf('students/%s',this.$route.params.id)
this.$rest(uri).get()
.success(data=>{
this.info = data
}).error(response=>{
this.toast(response.body.message||'获取详情失败','warn')
})
}
}
}



